import React, {useState} from 'react';
import 'antd/dist/antd.css';
import {Button, Table} from 'antd';

function Result_table(data_source: any) {
  const columns = [
    {
      title: "range",
      dataIndex: "range",
    },
    {
      title: "standardValue",
      dataIndex: "standardValue"
    },
    {
      title: "taskCode",
      dataIndex: "taskCode",
    },
    {
      title: "testValue",
      dataIndex: "testValue",
    },
    {
      title: "instrumentCode",
      dataIndex: "instrumentCode",
    },
    {
      title: "testStandardValue",
      dataIndex: "testStandardValue",
    },
    {
      title: "img",
      dataIndex: "img",
    },
    {
      title: "unit",
      dataIndex: "unit",
    },
    {
      title: "serialNumber",
      dataIndex: "serialNumber",
    }
  ], [data, setData] = useState([]);

  function update() {

    fetch("http://[2001:da8:270:2020:f816:3eff:fe32:f8da]:8001/download", {method: 'GET'}).then(
        function (res) {
          res.json().then(function (get_data) {
            const stream_data = get_data;
            let url_seq = []
            for (let i = 0; i < stream_data.length; i++) {
              url_seq[i] = stream_data[i].img;
              url_seq[i] = "http://[2001:da8:270:2020:f816:3eff:fe32:f8da]:8002/images/".concat(url_seq[i]);
              stream_data[i].img = <Button href={url_seq[i]}>Show</Button>
            }
            setData(stream_data)
          })
        }
    )

  }


  return (
      <div>
        <Button
            onClick={() => update()} type="primary"
        >Update</Button>
        <Table
            columns={columns}
            dataSource={data}
            bordered
            title={() => "展示表"}
            footer={() => "SUDA"}
        />
      </div>
  )
}


function App() {
  return (
      <div>
        {/* eslint-disable-next-line react/jsx-pascal-case */}
        <Result_table/>
      </div>
  );
}

export default App;